import react, { useState } from 'react';
 
const App=()=>{
  let time=new Date().toLocaleTimeString();
  const[ctime,updtime]=useState(time);
  const updt=()=>{
    time=new Date().toLocaleTimeString();
    updtime(time);
  }
  setInterval(updt,1000);
  return(
    <>
    <div className='df'>
    <img src="https://images-na.ssl-images-amazon.com/images/I/61OXKrkiy-L._SL1399_.jpg" />
    <h1 className="dfi">{ctime}</h1>
    </div>
    </>
  );
};
export default App;
 